*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body{
    background-color: #1B1717;
    height: 35%;
    width: 570px;
    position: absolute;
    margin: auto;
    top: 50px;
    left: 0;
    right: 0;
    padding: 30px 0;
    border-radius: 8px;
}

.header {
    position: relative;
    margin: auto;
    text-align: center;
    margin: 15px;

    color: #EEEBDD;
}

.form-input{
    position: relative;
    padding: 30px 15px;
    background: #EEEBDD;
    border-radius: 5px;
    box-shadow: 0 15px 30px rgba(0,0,0,0.3);
}

.form-input input{
    border-radius: 5px;
    width: 85%;
    height: 45px;
    border: 2px solid #EEEBDD;
    padding: 12px;
    color: #111111;
    font-weight: 500;
    position: relative;
}

.form-input input:focus{
    outline: none;
    border-color: #810000;
}

.form-input button{
    position: relative;
    float: right;
    width: 60px;
    height: 45px;
    border-radius: 3px;
    font-weight: 500;
    font-size: 16px;
    background: #810000;
    color: #EEEBDD;
    border: none;
    outline: none;
    cursor: pointer;
}

.todo-list{
    width: 100%;
    position: relative;
    margin-top: 40px;
    background: #EEEBDD;
    padding: 30px 20px;
    border-radius: 5px;
}

ul {
    position: relative;
    background: #EEEBDD;
    padding: 5px 10px;
    border-radius: 0px;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    
}

 ul li{
    display: flex;
    font-size: 15px;
    padding: 10px 20px;
    font-weight: 400;
    cursor: pointer;  
    transition: .5s;  
    border-bottom: 2px solid #EEEBDD;
}
ul li button {
    border: none;
    background: #810000;
    height: 100%;
    width: 100px;
    padding: 5px;
    margin: 0px 2px 0px 2px;
    color: #EEEBDD;
    border-radius: 5px;
    cursor: pointer;
}

.completed{
    text-decoration: none;
    opacity: 0.7;
}

.button{
    position: absolute;
    right: 16px;

}



